</style>


<script>
import { print } from 'util';
import { setTimeout } from 'timers';
import $ from 'jquery'
// @ is an alias to /src

export default {
  name: 'detail',

  data() {
      return {
          subicon:{
              "vertical-align": "-6px",
              backgroundPosition: "-660px -1938px",
              width: "24px",
              height: "24px",
              "margin-top":"10px",
              "margin-left":"16px",
              "margin-right":"7px",
              backgroundImage: "url("+require("../assets/icons.png")+")",
          },
          img:require("../assets/icons.png"),
          animeName: "",
          note: {
                backgroundImage: "url(" + require("@/assets/animeImg/辉夜大小姐想让我告白~天才们的恋爱头脑战~.png") + ")",
                backgroundPosition: "center center",
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
                width:"100%",
                height:"400px",
                position:"relative",
                "box-sizing": 'border-box',
                "z-index": '1',
          },
          comment:"",
          url:""
      }
  },

    methods:{
        //订阅番剧的click函数
        addChase:function(){
            if(localStorage.getItem('isLogin') == 0 || localStorage.getItem('isLogin') == null){
                this.$message({
                    type:'info',
                    message:'请先登录！'
                })
                this.$router.push('/login')
                return
            }
            fetch(this.$store.state.backend + '/addChase',{
                method:'POST',
                mode:'cors',
                body:JSON.stringify({
                    'username': localStorage.getItem('username'),
                    'a_name': this.animeName
                })
            }).then(res => res.text())
              .then(data => {
                this.$message({
                    type:'info',
                    message:data
                })
              })
              .catch(err => {
                this.$message({
                    type:'info',
                    message:'网络连接失败'
                })
              })
        },

        outsubscribe:function(e){
            var a = document.getElementById('subscribe-wrap')
            if(a.className == "dissubed"){
                a.style.backgroundColor = "#f36392"
            }
        },

        hoversubscribe:function(){
            var a = document.getElementById('subscribe-wrap')
            if(a.className == "dissubed"){
                a.style.backgroundColor = "#ff85ad"
            }

        },

        sub:function(){
            if(localStorage.getItem('isLogin') == 0 || localStorage.getItem('isLogin') == null){
                return
            }
            var a = document.getElementById('subscribe-wrap')
            if(a.className == "dissubed"){
                a.className = "subed"
                a.style.backgroundColor = "hsla(0, 0%, 100%, .12)"
                a.style.border = "4px solid hsla(0,0%,100%,.5)"
                a.style.width = "120px"
                a.style.height ="40px"

                var c = document.getElementById("sub-icon")
                c.style.backgroundImage = "url(" + require("../assets/icons.png")+")"
                c.style.backgroundPosition = "-660px -1938px"
                c.style.marginTop = "10px"
                c.style.marginLeft = "16px"

                var t = document.getElementById('sub-text')
                t.innerHTML="已追番"
                t.style.left="45px"
                t.style.top = "0px"
            }
            else{
                a.className = "dissubed"
                a.style.backgroundColor = "#f36392"
                a.style.border = "none"
                a.style.width = "128px"
                a.style.height ="48px"

                var c = document.getElementById("sub-icon")
                c.style.backgroundImage = "url(" + require("../assets/heart-bangumi.svg")+")"
                c.style.backgroundPosition = "unset"
                c.style.marginTop = "12px"
                c.style.marginLeft = "28px"

                var t = document.getElementById('sub-text')
                t.innerHTML="追番"
                t.style.left="62px"
                t.style.top = "4px"
            }
        },

        playAnime:function(){
            window.open(this.url)
        },

        hoverPlay:function(e){
            e.target.src = require("@/assets/play-pushed-.png")
        },

        outPlay:function(e){
            e.target.src = require("@/assets/play-light.png")
        },

        outmakeComment:function(e){
            var a = document.getElementById('makeComment-wrap')
            a.style.backgroundColor = "#f36392"
        },

        hovermakeComment:function(){
            var a = document.getElementById('makeComment-wrap')
            a.style.backgroundColor = "#ff85ad"
        },

        addLike:function(){
            var that = this
            $('#detail-comment-list').on("click", ".detail-zan-ico", function(e){
                if(e.target.nextSibling.style.color == 'rgb(255, 167, 38)'){
                    return
                }
                e.target.nextSibling.innerHTML = parseInt(e.target.nextSibling.innerHTML) + 1
                e.target.src = require("@/assets/orange-good.jpg")
                e.target.nextSibling.style.color='#ffa726'
                fetch(that.$store.state.backend + '/addLike',{
                    method:'POST',
                    mode:'cors',
                    body:JSON.stringify({
                        'com_id' : e.target.nextSibling.getAttribute('value')
                    })
                }).catch(err => {
                  that.$message({
                    type:'info',
                    message:'网络连接失败'
                    })
                })
            })
        },

        addProfile:function(){
            var that = this
            $('#detail-comment-list').on("click", "#detail-user-id", function(e){
                fetch(that.$store.state.backend + '/postFanName',{
                    method:'POST',
                    mode:'cors',
                    body:JSON.stringify({
                        'name' : e.target.innerHTML
                    })
                }).then(that.$router.push('profile'))
                  .catch(err => {
                    that.$message({
                        type:'info',
                        message:'网络连接失败'
                    })
                  })
            })
        },

        __trans:function(s){
            if(s==null) return '0'
            return s.replace(/[#:\/\\?*<>"| ()]/g,'_')
        },

        submitComment:function(){
            //alert(localStorage.getItem('isLogin'))
            if(localStorage.getItem('isLogin') == 0 || localStorage.getItem('isLogin') == null){
                this.$message({
                    type:'info',
                    message:'请登陆后评论！'
                })
                this.$router.push('login')
            }
            else{
                this.$prompt("你的评论：",{
                    confirmButtonText: '提交',
                }).then(({value}) => {
                    this.comment = value
                }).then(() => {
                    fetch(this.$store.state.backend + '/writeComment',{
                        method:'POST',
                        mode:'cors',
                        body: JSON.stringify({
                            'username':localStorage.getItem('username'),
                            'animeName':this.animeName,
                            'comment':this.comment,
                        })
                  }).then(res => res.json())
                    .then(data => {
                        var code = '<li id="detail-comment-item">\
                                        <div id="detail-user-id">\
                                            <span>'+ data['nickname'] +'</span>\
                                        </div>\
                                    <div id="detail-comment-contents">'+ data['content'] +'\
                                    </div>\
                                    <div id="detail-zan">\
                                        <img alt="zan-ico" id="detail-zan-ico" class="detail-zan-ico" src="@/assets/good.jpg" width="20px"><div id="detail-amount" value='+ data['com_id'] +'>0</div>\
                                        <div id="detail-date">'+ data['time'] +'</div> \
                                    </div>\
                                    </li>'
                        document.getElementById('detail-comment-list').innerHTML += code
                        var x = document.getElementsByClassName('detail-zan-ico')
                        x[x.length-1].src = require("@/assets/good.jpg")
                        window.scrollTo(0, document.body.scrollHeight)
                  }).catch(err => {
                        this.$message({
                            type:'info',
                            message:'网络连接失败'
                        })
                  })
                })
            }
        },

    },
     created(){
      fetch(this.$store.state.backend + '/loadDetail',{
        method:'GET',
        mode:'cors'
      }).then(res => res.json())
        .then(data =>{
            this.url = data['url']
            this.animeName = data['name']
            //加背景、图片、标题
            this.note.backgroundImage = "url(" + require("@/assets/animeImg/"+ this.__trans(data['name']) +".png") + ")"
            document.getElementById('detail-bangumi-img').src = require("@/assets/animeImg/"+ this.__trans(data['name']) +".png")
            document.getElementById('detail-bangumi-title').innerHTML = data['name']
            //加标签
            var code = ''
            for(var tag in data['tags']){
              code += "<span id='detail-tag'>"+ data['tags'][tag] +"</span>"
            }
            document.getElementById('detail-tags').innerHTML = code
            var that = this
            $('#detail-tags').on("click", "#detail-tag", function(e){
                that.$store.dispatch('categoryAction', e.target.innerHTML)
                that.$router.push('/')
            });
            //其它
            document.getElementById('detail-playtimes-data').innerHTML = data['playTimes']+'万'
            document.getElementById('detail-producer-data').innerHTML = data['animation']
            document.getElementById('detail-author-data').innerHTML = data['author']
            document.getElementById('detail-score').innerHTML = data['score']
            document.getElementById('detail-time').innerHTML = "放送时间："+ data['year'].toString() +"年"+ data['month'].toString() +"月开播"
            document.getElementById('detail-intro').innerHTML = data['intro']
            //加评论
            var code = ''
            for(var i=1; i<=data['commentDetail'][0]; i++){
              code += '<li id="detail-comment-item">\
                        <div id="detail-user-id">\
                            <span>'+ data['commentDetail'][i][0] +'</span>\
                        </div>\
                        <div id="detail-comment-contents">\
                            '+ data['commentDetail'][i][1] +'\
                        </div>\
                        <div id="detail-zan">\
                            <img alt="zan-ico" id="detail-zan-ico" class="detail-zan-ico" src="@/assets/good.jpg" width="20px"><div id="detail-amount" value='+ data['commentDetail'][i][4] +'>'+ data['commentDetail'][i][3] +'</div>\
                            <div id="detail-date">'+ data['commentDetail'][i][2] +'</div> \
                        </div>\
                    </li>'
            }
            document.getElementById('detail-comment-list').innerHTML = code
            var x = document.getElementsByClassName('detail-zan-ico')
            for(var i=0; i<x.length; i++){
                x[i].src = require("@/assets/good.jpg")
            }
            this.addProfile()
            this.addLike()
            window.scrollTo(0,0)

            fetch(this.$store.state.backend + '/subOrNot', {
                method:'POST',
                mode:'cors',
                body:JSON.stringify({
                    'username': localStorage.username,
                    'a_name': this.animeName
                })
            }).then(res => res.text())
                .then(data => {
                    var a = document.getElementById('subscribe-wrap')
                    if(data == 'alreadyChase'){
                        a.className = "subed"
                        a.style.backgroundColor = "hsla(0, 0%, 100%, .12)"
                        a.style.border = "4px solid hsla(0,0%,100%,.5)"
                        a.style.width = "120px"
                        a.style.height ="40px"
                    }
                    else{
                        a.className = "dissubed"
                        a.style.backgroundColor = "#f36392"
                        a.style.border = "none"
                        a.style.width = "128px"
                        a.style.height ="48px"

                        var c = document.getElementById("sub-icon")
                        c.style.backgroundImage = "url(" + require("../assets/heart-bangumi.svg")+")"
                        c.style.backgroundPosition = "unset"
                        c.style.marginTop = "12px"
                        c.style.marginLeft = "28px"

                        var t = document.getElementById('sub-text')
                        t.innerHTML="追番"
                        t.style.left="62px"
                        t.style.top = "4px"
                    }
                })
            })
        .catch(err => {
        this.$message({
            type:'info',
            message:'网络连接失败'
          })
        })
  }
}
</script>
